import React, { Component } from 'react'

class Logo extends Component {
    render() {
        return (
            <div className="header__logo-box">
                <a href="/">
                    <img
                        alt="Промальп НСК"
                        src="../icon.jpg"
                        className="header__logo"
                    />
                </a>
            </div>
        )
    }
}

export default Logo



