import PropTypes from 'prop-types'
import React, { Component } from 'react'

export default class WappLink extends Component {
    state = {
        number: this.props.number,
        name: this.props.name
    }
    render() {
        var { number, name } = this.state
        var pc = this.props.pc
        if (name === undefined)
            name = ` +${number[0]} ${number.substr(1, 3)} ${number.substr(
                4,
                3
            )} ${number.substr(7, 4)}`

        return (
            <a
                className="whatsapp-link"
                {...pc && `href=https://wa.me/${number}`}
            >
                <img src="whatsapp.jpeg" alt="" />
                {name}
            </a>
        )
    }
}

WappLink.propTypes = {
    number: PropTypes.string.isRequired,
    name: PropTypes.string
}



