import React, { Component } from 'react'
import { PropTypes } from 'prop-types'

export default class PriceBoxContent extends Component {
    render() {
        const content = this.props.content
        return (
            <div className="price-box__content">
                <div className="price-box__table">
                    <div className="row">
                        <div className="intro">{content.intro}</div>
                        <div className="icon">{content.icon || 'icon'}</div>
                    </div>
                    <div className="row">
                        <div className="job title">Наименование работ</div>
                        <div className="price title">
                            Цена за {content.units}
                        </div>
                    </div>
                    {Object.keys(content).map((item, index) => {
                        if (
                            item !== 'intro' &&
                            item !== 'icon' &&
                            item !== 'units'
                        ) {
                            return (
                                <div className="row" key={index}>
                                    <div className="job">{item}</div>
                                    <div className="price">
                                        {'от ' + content[item]}
                                    </div>
                                </div>
                            )
                        }
                        return null
                    })}
                </div>
            </div>
        )
    }
}

PriceBoxContent.propTypes = {
    content: PropTypes.object.isRequired
}



