import React, { Component } from 'react'
import { connect } from 'react-redux'
import PriceBoxMenu from './menu'
import PriceBoxContent from './content'

class Prices extends Component {
    state = {
        choice: 'metalworks'
    }
    onChoiceChange = item => {
        this.setState({
            choice: item
        })
    }
    render() {
        const prices = this.props.prices
        const choice = this.state.choice
        const toRender = prices[choice]
        const titles = prices.titles
        return (
            <div className="page-text">
                <h1 className="header-primary">Цены</h1>
                <div className="container price-box">
                    <PriceBoxMenu
                        categories={titles}
                        onChange={this.onChoiceChange}
                    />
                    <PriceBoxContent content={toRender} />
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        prices: state.prices
    }
}

export default connect(mapStateToProps)(Prices)



