import React, { Component } from 'react'

export default class CardDescription extends Component {
    render() {
        return (
            <figcaption className="gallery-card__description" onClick={this.props.onClick}>
                <div className="gallery-card__description--text" onClick={this.props.onClick}>
                    {this.props.text}
                </div>
            </figcaption>
        )
    }
}



