import React, { Component } from 'react'
import WappLink from 'components/wapp_link'
import PhoneLink from 'components/phone_link'

class Contacts extends Component {
    render() {
        var probablyPhone =
            /iphone|android|ie|blackberry|fennec/.test(
                navigator.userAgent.toLowerCase()
            ) && 'ontouchstart' in document.documentElement
        return (
            <div className="page-text">
                <h1 className="header-primary">Контакты</h1>
                Вы можете связаться с нами с помощью Whatsapp:
                <WappLink number="79137115101" pc={probablyPhone} />
                или позвонить по телефону:
                <PhoneLink number="+7-913-711-51-01" pc={probablyPhone} />
            </div>
        )
    }
}

export default Contacts



