import React, { Component } from 'react'
import { PropTypes } from 'prop-types'

export default class PriceBoxMenu extends Component {
    state = {
        selected: 0
    }
    render() {
        const categories = this.props.categories
        const selected = this.state.selected
        return (
            <div className="price-box__menu">
                {Object.keys(categories).map((item, key) => {
                    return (
                        <div
                            className={`price-box__link${
                                selected === key ? ' active' : ''
                            }`}
                            key={key}
                            onClick={() => {
                                this.props.onChange(item)
                                this.setState({
                                    selected: key
                                })
                            }}
                        >
                            {categories[item]}
                        </div>
                    )
                })}
            </div>
        )
    }
}

PriceBoxMenu.propTypes = {
    categories: PropTypes.object.isRequired
}



